@import url(https://fonts.googleapis.com/css?family=Damion|Muli:400,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("/index.png");
  /* background-image: url("https://wallpapercave.com/wp/wp2444481.jpg"); */
  /* background-image: url("http://images.unsplash.com/photo-1505739998589-00fc191ce01d?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjEyMDd9"); */

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html , body , #root , .App{
  height: 100%;
  width: 100%
}

.HomePageRoot {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center; }
  .HomePageRoot .HomePageHeader {
    position: absolute;
    top: 15vh;
    color: white; }

.LoginRoot {
  display: flex;
  flex-direction: column; }
  .LoginRoot .accountChangeMode {
    color: blue;
    cursor: pointer; }
  .LoginRoot form .formRow {
    display: flex;
    flex-direction: column;
    margin: 15px; }
  .LoginRoot .errorSpan {
    color: red; }

.Grid {
  display: flex;
  align-content: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  flex-flow: row; }
  .Grid .floatingButton {
    display: block;
    position: absolute;
    right: 50px;
    bottom: 50px; }
  .Grid .videoGridContainer {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 100%;
    align-items: center;
    justify-content: center; }
  .Grid iframe {
    margin: 10px; }
  .Grid .AddCamera {
    width: 100%;
    height: 100%;
    align-content: center;
    justify-content: center;
    align-items: center;
    justify-items: center;
    display: flex; }
    .Grid .AddCamera .cardContainer {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      width: 100%; }
      .Grid .AddCamera .cardContainer .buttons {
        margin-top: 15px;
        display: flex;
        justify-content: flex-end; }
        .Grid .AddCamera .cardContainer .buttons button {
          margin: 5px; }

